// @flow

import * as React from "react";
import Page from "../ui/Page";
import Logo from "../ui/Logo";
import { colors } from "../ui/theme";

function Header({ children }) {
  let style = {
    background: colors.textTitle
  };
  let styleText = {
    display: 'inline',
    margin: '0 20px',
    padding: '0 10px',
    color: colors.textTitle,
    background: colors.bg,
    fontSize: "16pt",
    textTransform: "uppercase",
    fontWeight: "900"
  };
  return (
    <div style={style}>
      <div style={styleText}>{children}</div>
    </div>
  );
}

function Section({ title, children }) {
  let style = {
    marginBottom: 40
  };
  let styleBody = {
    fontSize: "14pt",
    fontWeight: "400",
    color: "#333",
    marginTop: 20
  };
  return (
    <div style={style}>
      <Header>{title}</Header>
      <div style={styleBody}>{children}</div>
    </div>
  );
}

const whatWeDo = `
A group of software engineers.
`.trim();

function Index() {
  return (
    <Page>
      <Logo style={{ margin: "100px auto" }} />
      <Section title="Who we are">{whatWeDo}</Section>
      <Section title="Technology" />
      <Section title="Projects" />
      <Section title="Team" />
    </Page>
  );
}

export default Index;
